import React, { Component } from 'react';
import ReactDom from 'react-dom';
import Nav from './Nav';
import './index.css';
import * as serviceWorker from './serviceWorker';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import ClassNine from './ClassNine';
import {BrowserRouter as Router,
    Route,
    Switch,
    Link
} from 'react-router-dom';
import Primary from './Primary';
import Home from './Home';
import ClassTen from './ClassTen';
import Footer from './Footer';
class App extends Component{
    render(){
        return(
            <Router>
                <nav className="navigation-bar">
                    <input type="checkbox" id="check"></input>
                    <label for="check" className="checkbtn"><FontAwesomeIcon icon={faBars} /></label>
                    <label className="logo">Basic Tuition website</label>
                    <ul>
                        <li><Link to="/">Home</Link></li>
                        <li><Link to="Primary"> Primary</Link></li>
                        <li><Link to="ClassNine">Class 9</Link></li>
                        <li><Link to="ClassTen">Class 10</Link></li>
                    </ul>
                </nav>
               
                    <Switch>
                        <Route path="/basic-tuition-react/" exact><Home/></Route>
                        <Route path="/ClassNine"><ClassNine/></Route>
                        <Route path="/Primary"><Primary/></Route>
                        <Route path="/ClassTen"><ClassTen/></Route>
                    </Switch>
                <Footer/>
            </Router>
        );
    }
}
export default App;